<template>
    <section class="layout">

        <div class="admin" v-if="role === 'admin'">

            <section v-if="!isInDeletingMode && !isAdminToolbarLoading">
                <span v-on:click="beginFileSelect()">создать новость</span>
                <input type="file" id="fileInput" style="display: none;" />

                <span class="remove" v-on:click="isInDeletingMode = true">начать удаление</span>
            </section>

            <section v-if="isInDeletingMode && !isAdminToolbarLoading">
                <span v-on:click="cancelDeleting()">отмена</span>
                <span v-on:click="deleteNews()" class="remove" >удалить</span>
            </section>

            <LoadingSpinner v-if="isAdminToolbarLoading" />
        </div>

        <div class="news-row" v-for="row in rowedNews" :key="row[0].id">
            <LazyLoadImage v-for="item in row" :key="item.id"
                           v-on:click="imageClick(item)"
                           v-bind:url="getFullImgURL(item)"
                           v-bind:style="{'max-height': height + 'px'}"
                           v-bind:class="{'to-delete': !!newsToDelete.find((t) => t.id === item.id)}"
            />

        </div>

        <div class="bottom" v-observe-visibility="onBottomReached">
            <LoadingSpinner v-if="isLoading" />
        </div>

    </section>
</template>

<script>
    import { mapGetters } from 'vuex';
    import modules from "../store/all.modules.name"

    import {EventBus, Events} from "../common/eventBus";
    import LazyLoadImage from "./components/lazyLoadImage/LazyLoadImage";
    import LoadingSpinner from "./components/LoadingSpinner";

    import {NewsAPI} from "../api/news";

    import config from "../common/config"

    import Vue from 'vue'
    import VueObserveVisibility from 'vue-observe-visibility'
    Vue.use(VueObserveVisibility);

    const itemsPerPage = 4;

    export default {
        name: "Home",

        components: {LoadingSpinner, LazyLoadImage},

        data: () => {
            return {
                isInDeletingMode: false,
                newsToDelete: [],

                isLoading: true,
                isAdminToolbarLoading: false,

                lastPageNumber: 1,
                hasMoreToLoad: false,
                news: [],

                height: 0,
            }
        },

        methods: {
            async deleteNews() {
                this.isAdminToolbarLoading = true;
                this.isInDeletingMode = false;

                for (let i = 0; i < this.newsToDelete.length;i++) {
                    let res = await NewsAPI.delete(this.newsToDelete[i].id);
                    if (!res.ok) {
                        alert("Произошла ошибка.\n" + "(" + res.statusCode + ") " + res.statusText);
                        break
                    }
                }


                this.newsToDelete = [];
                this.isAdminToolbarLoading = false;
                this.loadNews();
            },

            cancelDeleting() {
                this.newsToDelete = [];
                this.isInDeletingMode = false;
            },

            beginFileSelect() {
                document.querySelector("#fileInput").click();
            },

            async onFileSelect() {
                this.isAdminToolbarLoading = true;

                let file = document.querySelector("#fileInput").files[0];
                await NewsAPI.uploadImg(file);
                this.hasMoreToLoad = false;
                this.news = [];

                this.isAdminToolbarLoading = false;

                await this.loadNews();
            },

            async loadNews() {
                this.isLoading = true;
                this.news = [];
                this.lastPageNumber = 1;

                let res = await NewsAPI.getNews(itemsPerPage);
                if (res.ok) {
                    let json = await res.json();

                    this.news = json.data;
                    this.hasMoreToLoad = json.hasMoreToLoad;
                }

                this.isLoading = false;
            },

            async loadMoreNews() {
                this.isLoading = true;

                let res = await NewsAPI.getMoreNews(this.lastPageNumber + 1, itemsPerPage);
                if (res.ok) {
                    let json = await res.json();

                    this.news = [...this.news, ...json.data];
                    this.hasMoreToLoad = json.hasMoreToLoad;
                    this.lastPageNumber++;
                }

                this.isLoading = false;
            },

            getFullImgURL(item) {
                return config.PROTECTED_API_URL() + "/news/image/" + item.id
            },

            imageClick(item) {
                if (this.isInDeletingMode) {
                    let res = this.newsToDelete.find((t) => t.id === item.id) ;
                    if (res) {
                        this.newsToDelete = this.newsToDelete.filter((t) => t.id !== item.id)
                    } else {
                        this.newsToDelete.push(item)
                    }

                } else {
                    EventBus.$emit(Events.showImagePreview, {img: this.getFullImgURL(item), height: 90})
                }

            },

            onBottomReached(isVisible) {
                if (!this.isLoading && isVisible && this.hasNews && this.hasMoreToLoad) {
                    this.hasMoreToLoad = false;
                    this.loadMoreNews();
                }
            }
        },

        computed: {
            ...mapGetters(modules.user, [
                "role"
            ]),

            rowedNews: function () {
                let res = [],
                    rowedNews = [];

                for (let i = 0; i < this.news.length;i++) {
                    if (rowedNews.length === 2) {
                        res.push(rowedNews);
                        rowedNews = [];
                    }

                    rowedNews.push(this.news[i]);

                    if (i === (this.news.length - 1)) {
                        res.push(rowedNews)
                    }
                }

                return res;
            },


            hasNews: function () {
               return this.news.length !== 0
            }

        },

        mounted() {
            this.height = document.documentElement.clientHeight - (200 + 10);

            if (!this.hasNews) {
                this.loadNews();
            }

            let res = document.querySelector("#fileInput");
            if (res) {
                res.addEventListener('change', this.onFileSelect, false);
            }
        },
    }
</script>

<style lang="sass" scoped>
    @import '../assets/basic'

    .layout
        width: 90%
        max-width: 1200px
        margin: auto
        font-family: Roboto, sans-serif

        .admin
            display: flex
            justify-content: center
            margin-bottom: 25px

            svg
                height: 19px

            .remove
                color: #f44336!important

                &:hover
                    background-color: #f443361f!important

            span, input
                margin: 10px

            span
                font-weight: bold
                font-size: 14px
                color: #1d5db5
                cursor: pointer
                text-align: center
                padding: 5px
                border-radius: 5px
                transition: 0.35s

                &:hover
                    background-color: #1d5db524


        .news-row
            display: flex
            justify-content: space-evenly

            .to-delete
                box-shadow: 0px 0px 10px 3px #f44336

            img
                cursor: pointer
                height: auto
                width: auto
                max-width: 50%
                align-self: center

        .bottom
            display: flex
            justify-content: center
            height: 100px

    .title
        text-align: center

    .placeholder
        display: block
        margin-top: 1.5rem
        margin-bottom: 1.5rem
        height: 300px
        background: url(/0001.jpg) center
</style>