import APIService from "./index";

export const NewsAPI = {
    uploadImg(file) {
        return APIService.protectedPostFile("/news/uploadImage", file)
    },

    getNews(itemsPerPage) {
        return APIService.protectedPost("/news/getNews", {itemsPerPage})
    },

    getMoreNews(pageNumber, itemsPerPage) {
        return APIService.protectedPost("/news/getMoreNews", {pageNumber, itemsPerPage})
    },

    delete(id) {
        return APIService.protectedPost("/news/delete", {id})
    },
};